import React, { useState } from 'react';
import './VideoSidebar.css'
import FavoriteIcon from '@material-ui/icons/Favorite';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import MessageIcon from '@material-ui/icons/Message';
import ShareIcon from '@material-ui/icons/Share';
import { Avatar, IconButton } from '@material-ui/core';

const VideoSidebar = (props) => {
    const [liked, setLiked] = useState(props.userLiked);

    const likeDislike = () => {
        if(liked) {
            setLiked(false);
        } else {
            setLiked(true);
        }
    }

    return (
        <div className='video_sidebar'>
            <div className='video_sidebar__button'>
                <Avatar 
                    fontSize='large' 
                    src={props.profileImage}
                />
            </div>
            <div className='video_sidebar__button'>
                {
                    liked ? 
                    (<FavoriteIcon fontSize='large' onClick={likeDislike} />) : 
                    (<FavoriteBorderIcon fontSize='large' onClick={likeDislike} />)
                }
                <p>{liked ? props.likes + 1 : props.likes}</p>
            </div>
            <div className='video_sidebar__button'>
                <MessageIcon fontSize='large' />
                <p>{props.comments}</p>
            </div>   
            <div className='video_sidebar__button'>
                <ShareIcon fontSize='large' />
                <p>{props.shares}</p>
            </div>               
        </div>
    )
}

export default VideoSidebar;
